<template>
    <div class='entranceSign'>
        <div class="topInfo">
            <h2 class='title'>入场签到</h2>
        </div>
        <Table  :tableData="tableData" :tableQuery="tableQuery"  :tabhide="tabhide" :tableKey="tableKey" ref="table" :total="total" :isPage = "isPage" :pageSize = "pageSize"  v-on:getQueryParam = "getQueryParam"
          v-on:changeVal="changeParam"></Table>
    </div>
</template>
<script>
import Table from '../../components/Table'
import {entranceSignList, getallbranch, entranceSearchId} from '../../services/service.js'
import {isApiResponseOk, setSessionStorage} from '../../utils/com.js'
export default {
  data () {
    return {
      tableQuery: [
        {
          name: 'startTime',
          type: 'start_date',
          model: '',
          title: '选择开始日期',
          format: 'yyyy-MM-dd HH:mm:ss',
          text: '时间: ',
          class: 'startData'
        },
        {
          name: 'student', 
          title: '输入姓名或手机号',
          type: 'input',
          model: '',
          text: '学员: '
        },
        {
          name: 'branchOfficeId',
          title: '请选择',
          type: 'select',
          model: '',
          text: '门店: ',
          selectArr: []
        }
      ],
      tableData: [],
      tableKey: [
        {name: '学员/潜客ID', value: 'entranceUserId', nor: true},
        {name: '学员姓名', value: 'userName', nor: true},
        {name: '学员类别', value: 'status', studentKind: true},
        {name: '联系方式', value: 'phone', nor: true},
        {name: '门店', value: 'branchOfficeName', nor: true},
        {name: '签到时间', value: 'enterTime', 'appointTime': true}
      ],
      isShowSelectionBtn: false,
      isPage: true,
      total: 0,
      currentPage: 1,
      pageSize: 10,
      tabhide: false,
      pageNumber: 0,
      time: '',
      key: '',
      branchOfficeId: ''
    }
  },
  components: {
    Table
  },
  created () {
    let that = this
    this.$nextTick(_ => {
      this.initData().then(() => {
        this.initbranch()
      })
    })
  },
  methods: {
    // 获取查询参数
    getQueryParam (queryObj) {
      console.log(queryObj)
      this.time = queryObj.startTime
      this.key = queryObj.student
      this.branchOfficeId = queryObj.branchOfficeId
      this.initData()
    },
    changeParam (page) {
      console.log(page)
      this.currentPage = page
      this.initData()
      // console.log('获取数据', data)
    },
    async initbranch () {
      const res = await getallbranch()
      if (isApiResponseOk(res)) {
        let branchList = []
        for (var i=0; i<res.data.length; i++) {
          let obj = {}
          obj.value = res.data[i].id
          obj.label = res.data[i].name
          branchList.push(obj)
        }
        // console.log(branchList)
        this.$refs.table.reloadValue('branchOfficeId', branchList)
      }
    },
    // 获取签到记录
    async initData () {
      const res = await entranceSignList(this.currentPage, this.pageSize, this.time, this.key, this.branchOfficeId)
      if (isApiResponseOk(res)) {
        if (res.data) {
          this.total = res.data.totalNum
          this.tableData = res.data.record
          // for (var i=0; i<this.tableData.length; i++) {
          //   this.tableData[i].seqNo = i+1
          // }
          if (this.total === 0) {
            this.isPage = false
          } else {
            this.isPage = true
          }
        }
      } else {
        this.isPage = false
      }
    }
  }
}
</script>
<style lang="sass" scoped>
.entranceSign
  .topInfo
    .title
      font-family: PingFangSC-Semibold;
      font-size: 0.24rem;
      color: #263238;
      letter-spacing: 0;
      line-height: 0.24rem;
      font-weight: 548;
  .table
    margin-top: 0.4rem;
</style>
<style>
  .entranceSign .table .el-col div{
    margin-right: 0.1rem;
  }
</style>




